/* About Section Starts */
.aboutSection {
  width: 100vw;
  display: flex;
  gap: 5rem;
  align-items: center;
  justify-content: flex-start;
}

.aboutLeft {
  width: 100%;
}

.aboutImg {
  width: 100%;
  border-radius: 1rem;
  height: 75vh;
}

.aboutRight {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4rem;
}

.aboutPara {
  font-size: 2rem;
  color: var(--darkblue);
  line-height: 3rem;
  font-weight: 500;
}

@media (max-width: 1200px) {
  .aboutPara {
    font-size: 1.8rem;
  }
}

/* Media Queries */
@media (max-width: 991px) {
  .aboutLeft,
  .aboutRight {
    text-align: center;
    justify-content: center;
    align-items: center;
  }

  .aboutSection {
    flex-direction: column;
  }

  .aboutImg {
    width: 80%;
    height: auto;
  }
}

@media (max-width: 768px) {
  .aboutImg {
    width: 100% !important;
  }
}
