.contactSection {
  width: 100vw;
  text-align: center;
  background: var(--bg-shade);
  padding-top: 5rem;
  padding-bottom: 2rem;
}

.contactForm {
  margin-top: 5rem;
  max-width: 768px;
  margin: 5rem auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 3rem;
}

.flexInput {
  display: flex;
  align-items: center;
  gap: 3rem;
  width: 100%;
}

.flexInput div,
.textAreaDiv {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  width: 100%;
}

label {
  font-size: 2rem;
  font-weight: 500;
  color: --var(darkblue);
}

input, textarea {
  width: 100%;
  padding: 1.5rem;
  outline: none;
  border: 1px solid var(--primary);
  border-radius: 0.5rem;
  font-size: 1.8rem;
}

textarea {
  resize: none;
  min-height: 20rem;
}


@media (max-width: 576px) {
    .flexInput {
        flex-direction: column;
    }
}