/* Hero Section Starts */
.heroSection {
  width: 100vw;
  height: calc(100vh - 10rem);
  display: flex;
  align-items: center;
  gap: 5rem;
  background: var(--bg-shade);
  margin-top: 9rem;
}

.sectionLeft {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 2.5rem;
}

.subHeading {
  font-size: 2rem;
  text-transform: capitalize;
}

.heading {
  font-size: 6rem;
  color: var(--heading-color);
}

.heroPara {
  font-size: 2rem;
  line-height: 3rem;
  color: var(--darkblue);
  font-weight: 500;
}

.sectionRight {
  width: 100%;
  text-align: center;
}

.heroImg {
  width: 100%;
  height: 65vh;
}

@media (max-width: 1040px) {
  .heroSection {
    height: auto;
    flex-direction: column-reverse;
  }

  .sectionLeft {
    align-items: center;
    text-align: center;
  }

  .heroImg {
    width: 80%;
    height: auto;
  }
}

@media (max-width: 768px) {
  .heroImg {
    width: 100% !important;
  }
}

@media (max-width: 576px) {
  .heading {
    font-size: 4rem;
  }

  .heroPara {
    font-size: 1.8rem;
  }
}
