/* PortFolio Section */
.portfolioSection {
  width: 100vw;
  background-color: var(--bg-shade);
}

.portfolioHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.arrowDiv {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.arrows {
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
  background-color: var(--github);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  cursor: pointer;
  color: var(--white);
}

.portfolioCategories {
  margin-top: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
}

.categoryBtn {
  border: none;
  background-color: var(--darkblue);
  color: white;
  font-weight: 600;
  font-size: 2rem;
  padding: 1rem 3rem;
  border-radius: 0.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.categoryBtn:hover,
.active {
  background-color: var(--primary) !important;
}

.portfolioContaier {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 5rem;
  gap: 5rem;
}

@media (max-width: 480px) {
  .portfolioHeader {
    flex-wrap: wrap;
    gap: 3rem;
    align-items: center;
    justify-content: center;
  }
}
