.card {
  background-color: var(--white);
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
  max-width: 40rem;
  border-radius: 1rem;
}

.cardImg {
  width: 100%;
  min-height: 25rem;
  border-radius: 0.5rem;
}

.cardBody {
  padding: 5rem 3rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.cardTitle {
  font-size: 2.5rem;
  text-transform: capitalize;
}

.cardCategory {
  text-transform: uppercase;
  font-size: 2rem;
  color: var(--primary);
  margin-top: 0.5rem;
  font-weight: 600;
}

.cardDescription {
  font-size: 1.8rem;
  font-weight: 500;
  color: #666;
  text-align: justify;
}

.cardButtons {
  display: flex;
  gap: 2rem;
  align-items: center;
  margin-top: 1rem;
}

.cardBtn {
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: center;
  padding: 1rem 2rem;
}

@media (max-width: 1200px) {
  .card {
    max-width: 38rem;
  }
}

@media (max-width: 1040px) {
  .card {
    max-width: 100%;
  }
}

@media (max-width: 350px) {
  .cardButtons {
    flex-direction: column;
  }
}